<template>
    <div class="ion-picker-cmp" :class="[modeClass,cssClass]">
        <Backdrop :enableBackdropDismiss="enableBackdropDismiss" :isActive="isActive" :bdClick="bdClick"></Backdrop>
        <transition
                name="picker"
                @before-enter="beforeEnter"
                @after-enter="afterEnter"
                @before-leave="beforeLeave"
                @after-leave="afterLeave">
            <div class="picker-wrapper" v-show="isActive">
                <div class="picker-toolbar">
                    <div v-for="(b,index) in buttons"
                         :key="index"
                         class="picker-toolbar-button"
                         :class="[b.cssRole]">
                        <Button @click="btnClick(b)" :class="b.cssClass" class="picker-button" clear>{{b.text}}</Button>
                    </div>
                </div>
                <div class="picker-columns">
                    <div class="picker-above-highlight"></div>
                    <PickerCol v-for="(c,index) in columns"
                               :index="index"
                               :key="c.name"
                               :col="c"
                               @onChange="colChange"></PickerCol>
                    <div class="picker-below-highlight"></div>
                </div>
            </div>
        </transition>
    </div>
</template>
<style lang="scss">
    @import "./picker.scss";
    @import "./picker.ios.scss";
    @import "./picker.md.scss";

    // transitioName = 'picker'
    .picker-enter-active, .picker-leave-active {
        transform: translateY(0%);
    }

    .picker-enter, .picker-leave-active {
        transform: translateY(100%);
    }
</style>
<script type="text/javascript">
  /**
   * @component Picker
   * @description
   *
   * ##  / Picker
   *
   * ### 
   *
   * Picker, Select, Select, . , , Picker, Demo.
   *
   * ### 
   *
   * Picker, , Datetime/CitySelect. Picker. :
   *
   *
   * #### 1. buttons
   *
    / Name |  / Type |  / Description
   ----------|-----------|--------------------------------
   text      | string    | 
   role      | string    | , rolecancel, 
   handler   | function  | 
   *
   * #### 2. columns
   *
    / Name     |  / Type          |  / Description
   --------------|--------------------|---------------------------
   name          | string             | column
   align         | string             | column
   selectedIndex | number             | column(index)
   prefix        | string             | 
   prefixWidth   | string             | , '50px'
   suffix        | string             | 
   suffixWidth   | string             | , '50px'
   cssClass      | string             | column, 
   columnWidth   | string             | column, 
   optionsWidth  | string             | column
   options       | PickerColumnOption | column
   *
   *
   * #### 3. columnsoptions
   *
    / Name |  / Type |  / Description
   ----------|-----------|------------------
   text      | string    | 
   value     | *         | 
   disabled  | boolean   | 
   *
   *
   * #### 4. onChange
   *
   * , .
   *
   * #### 5. onSelect
   *
   * , 
   *
   * ### 
   *
   * , , `Picker.present(...)`
   *
   * ```
   *  import { Picker } from 'vimo/components/picker'
   * ```
   *
   * @demo https://dtfe.github.io/vimo-demo/#/picker
   * */
  import { isString, isPresent, isNumber, hashChange } from '../../util/util'
  import PickerCol from './picker-col.vue'
  import { Backdrop } from '../backdrop'
  import { Button } from '../button'
  export default{
    name: 'Picker',
    data () {
      return {
        isActive: false,        // 
        enabled: true,          // ()

        dismissCallback: null,  // 
        presentCallback: null,  // 

        cols: [],               // 
        timer: null,            // 
        unreg: null             // 
      }
    },
    props: {
      buttons: {
        type: Array,
        required: true
      },
      columns: {
        type: Array,
        required: true
      },
      mode: {
        type: String,
        default () { return this.$config.get('mode') || 'ios' }
      },
      cssClass: String,
      enableBackdropDismiss: {
        type: Boolean,
        default: true
      },
      dismissOnPageChange: {
        type: Boolean,
        default: true
      },
      onChange: Function,
      onSelect: Function,
      onDismiss: Function
    },
    computed: {
      modeClass () {
        return `picker-${this.mode}`
      }
    },
    methods: {
      /**
       * Component Animate Hooks
       * @private
       * */
      beforeEnter () {
        this.enabled = false // 
        this.$app && this.$app.setEnabled(false, 400)
      },
      afterEnter () {
        this.presentCallback()
        this.enabled = true
      },
      beforeLeave () {
        this.enabled = false
        this.$app && this.$app.setEnabled(false, 400)
      },
      afterLeave () {
        this.dismissCallback()
        // DOM
        this.$el.remove()
        this.enabled = true
      },

      /**
       * backdrop
       * @private
       * */
      bdClick () {
        if (this.enabled && this.enableBackdropDismiss) {
          this.dismiss()
        }
      },

      /**
       * @function present
       * @param {object} options - 
       * @param {Object} options.buttons - button
       * @param {Array} options.columns - column
       * @param {String} options.column.name - column
       * @param {String} options.columns.align - column
       * @param {String} [options.mode='ios'] - 
       * @param {String} [options.cssClass] - 
       * @param {Boolean} [options.enableBackdropDismiss=true] - backdrop
       * @param {Function} [options.onChange=noop] - picker, col, 
       * @param {Function} [options.onSelect=noop] - 
       * @description
       * 
       * */
      present () {
        this.isActive = true
        return new Promise((resolve) => { this.presentCallback = resolve })
      },

      /**
       * @function dismiss
       * @description
       * 
       * */
      dismiss () {
        this.isActive = false
        this.unreg && this.unreg()
        this.onDismiss && this.onDismiss()
        return new Promise((resolve) => { this.dismissCallback = resolve })
      },

      /**
       * (/)
       * @private
       * */
      btnClick (button) {
        if (!this.enabled) {
          return
        }

        let shouldDismiss = true

        if (button.handler) {
          // a handler has been provided, execute it
          // pass the handler the values from the inputs
          if (button.handler(this.getSelected()) === false) {
            // if the return value of the handler is false then do not dismiss
            shouldDismiss = false
          }
        }

        if (shouldDismiss) {
          this.dismiss(button.role)
        }
      },

      /**
       * , 
       * @return {Array} selected - selected
       * @return {String} selected.text - text
       * @return {String} selected.value - value
       * @return {String} selected.columnIndex - columnIndex
       * @private
       * */
      getSelected () {
        let selected = {}
        this.columns.forEach((col, index) => {
          let selectedColumn = col.options[col.selectedIndex]
          selected[col.name] = {
            text: selectedColumn ? selectedColumn.text : null,
            value: selectedColumn ? selectedColumn.value : null,
            columnIndex: index
          }
        })
        return selected
      },

      /**
       * ,
       * @private
       * */
      colChange (data) {
        // colonSelect, col
        this.$emit('onSelect', data)
        this.onSelect && this.onSelect(data)

        // col, , onChange
        let selectedData = this.getSelected()
        this.$emit('onChange', selectedData)
        this.onChange && this.onChange(selectedData)
      },

      /**
       * data
       *
       * PS: this.data
       *
       * - this.buttons: string, button
       * - this.buttons: role, cssRole: `picker-toolbar-${button.role}`
       * - columns -> column.options: , toStringtext/value
       *
       * @private
       * */
      normalizeData () {
        // normalize the data
        this.buttons = this.buttons.map(button => {
          if (isString(button)) {
            return {text: button}
          }
          if (button.role) {
            // role: cancel / button
            button.cssRole = `picker-toolbar-${button.role}`
          }
          return button
        })

        // clean up dat data
        this.columns = this.columns.map(column => {
          if (!isPresent(column.options)) {
            column.options = []
          }
          // column.selectedIndex = 0
          column.options = column.options.map(inputOpt => {
            // PickerColumnOption
            let opt = {
              text: '',
              value: '',
              disabled: inputOpt.disabled
            }

            if (isPresent(inputOpt)) {
              if (isString(inputOpt) || isNumber(inputOpt)) {
                opt.text = inputOpt.toString()
                opt.value = inputOpt
              } else {
                opt.text = isPresent(inputOpt.text) ? inputOpt.text : inputOpt.value
                opt.value = isPresent(inputOpt.value) ? inputOpt.value : inputOpt.text
              }
            }
            return opt
          })
          return column
        })
      },

      /**
       * , 
       * @private
       * */
      recordChildComponent (childComponent) {
        this.cols.push(childComponent)
      },

      // -------  --------
      /**
       * ,
       * */
      resetColumn (index) {
        this.cols[index].reset()
      },

      /**
       * , , 
       * @private
       * */
      refresh () {
        this.cols.forEach(column => {
          column.refresh()
        })
      }
    },
    created () {
      this.normalizeData()

      // dismissOnPageChange
      if (this.dismissOnPageChange) {
        this.unreg = hashChange(() => {
          this.isActive && this.dismiss()
        })
      }
    },
    beforeMount () {
      let pickerCmpElements = document.querySelectorAll('.ion-picker-cmp')
      if (pickerCmpElements.length > 0) {
        pickerCmpElements.forEach((ele) => {
          ele.remove()
          alert('beforeMount find Picker opened!')
        })
      }
    },
    components: {Backdrop, PickerCol, Button}
  }
</script>



